import React from "react";

import Select, { SelectChangeEvent } from "@mui/material/Select";
import {
  Box,
  FormControl,
  IconButton,
  InputLabel,
  MenuItem,
} from "@mui/material";
import { AMM } from "../../../container/amm";

import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import ArrowDropUpIcon from "@mui/icons-material/ArrowDropUp";
import { Typography } from '@mui/material';

function MarketSelector() {
  const MarketList = [
    "BTCUSD",
    "ETHUSD",
    "DOGEUSD",
    "AAVEUSD",
    "BNBUSD",
    "AVAXUSD",
    "LINKUSD",
    "SOLUSD",
  ];

  const iconUrl = (icon: string) => `/assets/${icon}.png`;

  const { marketticker, setMarketTicker } = AMM.useContainer();
  const [open, setOpen] = React.useState(false);
  const [iconName, setIconName] = React.useState("/assets/ETHUSD.png");

  const handleChange = (event: SelectChangeEvent<typeof marketticker>) => {
    setIconName(iconUrl(event.target.value));
    setMarketTicker(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  // const iconImage = marketticker.substring(0, marketticker.length - 3).toLowerCase() + "Icon";

  return (
    <FormControl sx={{ position: "relative", width: "220px", height: "58px" }}>
      <Box
        sx={{
          width: "220px",
          height: "58px",
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <Box
          component="img"
          src={iconName}
          sx={{
            ml: 2,
            width: 36,
            height: 36,
          }}
        />
        <Box height="100%" width="100px" pt="18px">
          <Typography
            sx={{
              textAlign: 'left',
              fontSize: 12,
              color: "rgb(169, 168, 166)",
              fontFamily: "Montserrat Light",
            }}
          >
            Market
          </Typography>
        </Box>
        {open ? (
          <ArrowDropUpIcon sx={{ color: "rgb(236, 232, 227)" }} />
        ) : (
          <ArrowDropDownIcon sx={{ color: "rgb(236, 232, 227)" }} />
        )}
      </Box>

      <Select
        labelId="demo-simple-select-label"
        id="demo-simple-select"
        defaultValue={"ETHUSD"}
        open={open}
        onClose={handleClose}
        onOpen={handleOpen}
        onChange={handleChange}
        inputProps={{ IconComponent: () => null }}
        sx={{
          width: "220px",
          position: "absolute",
          border: "1px solid rgba(255, 255, 255, 0.12)",
          borderRadius: "10px",
          color: "rgb(236, 232, 227)",
          fontFamily: "Montserrat Medium",
          height: "58px",
          // backgroundColor: "rgba(255, 255, 255, 0.04)",
          // '&:hover': {
          //   backgroundColor: "rgba(255, 255, 255, 0.1)",
          // },
          "& .MuiSelect-outlined": {
            display: "flex",
            justifyContent: "center",
            alignItems: "end",
            padding: "0 !important",
            height: "56px !important", 
            width: "260px",
            lineHeight: "240%",
          },
          "&.Mui-focused .MuiOutlinedInput-notchedOutline": { border: "none" },
        }}
        MenuProps={{
          PaperProps: {
            sx: {
              bgcolor: "#131312",
              color: "rgb(236, 232, 227)",
              borderRadius: "10px",
              boxSizing: "border-box",
              width: "220px",
              border: "1px solid rgba(255, 255, 255, 0.12)",
              left: "24px !important",
              top: "152px !important",
              "& .MuiList-root": {
                p: "0",
              },
              "& .MuiMenuItem-root": {
                p: "12px 16px",
              },
              "& .MuiMenuItem-root:not(:last-child)": {
                borderBottom: "1px solid rgba(255, 255, 255, 0.12)",
              },
            },
          },
        }}
      >
        {MarketList.map((market) => (
          <MenuItem key={market} value={market} sx={{ color: "white" }}>
            {market}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}

export default MarketSelector;
