import { useState, useCallback, useEffect } from "react";
import { useSelector } from "react-redux";
import { IReduxState } from "../../../store/slices/state.interface";
import { calcMarketDetails } from "../../../store/slices/markets-slice";
import { useDispatch } from "react-redux";
import { Trade } from "../../../container/trade";
import { AMM } from "../../../container/amm";
import { AppDispatch } from "../../../store/store";

import { Box, Grid, Typography } from "@mui/material";

import MarketSelector from "./Marketselector";
import MobilePriceHeader from "./MobilePriceHeader";
import useResponsive from "../../../hooks/useResponsive";

function PriceHeader() {
  const dispatch = useDispatch<AppDispatch>();
  const { perpetualObject } = Trade.useContainer();
  const { marketticker } = AMM.useContainer();
  const isDesktop = useResponsive("up", "md");
  const isMarketInfoCalculated = useSelector<IReduxState, boolean>((state) => {
    return state.markets.isMarketInfoCalculated;
  });

  const isMarketInfoInitialized = useSelector<IReduxState, boolean>((state) => {
    return state.markets.isMarketInfoInitialized;
  });

  const marketIndexPrice = useSelector<IReduxState, string>((state) => {
    return state.markets.marketIndexPrice;
  });

  const CalcMarketDetails = function () {
    dispatch(
      calcMarketDetails({
        perpetualObject: perpetualObject,
        tickerSymbol: marketticker,
      })
    );
  };

  useEffect(() => {
    CalcMarketDetails();
    const timer = setInterval(() => CalcMarketDetails(), 300000);
    return () => clearInterval(timer);
  }, [marketticker]);

  return (
    <Box
      sx={{
        backgroundColor: "#131312",
        py: isDesktop ? 2 : 0,
        px: isDesktop ? 3 : 0,
      }}
    >
      {isDesktop && (
        <Box
          sx={{
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "center",
            flexDirection: { xs: "column", sm: "row" },
          }}
        >
          <MarketSelector />
          <Box
            sx={{
              display: "flex",
              justifyContent: "flex-start",
              pl: 2,
              ml: 2,
              py: 1,
              border: "1px solid rgba(255, 255, 255, 0.12)",
              borderRadius: "10px",
              flexGrow: "1",
            }}
          >
            <Box sx={{ pr: { xs: 2, md: 3, xl: 4 } }}>
              <Typography
                sx={{
                  fontSize: "0.75rem",
                  color: "rgb(169, 168, 166)",
                  fontFamily: "Montserrat Light",
                }}
              >
                Index Price
              </Typography>
              {isMarketInfoCalculated ? (
                <Typography
                  sx={{
                    color: "rgb(236, 232, 227)",
                    fontFamily: "Montserrat",
                  }}
                >
                  {marketIndexPrice}
                </Typography>
              ) : (
                [
                  isMarketInfoInitialized ? (
                    <Typography
                      sx={{
                        color: "rgb(236, 232, 227)",
                        fontFamily: "Montserrat",
                      }}
                    >
                      {marketIndexPrice}
                    </Typography>
                  ) : (
                    <Typography
                      sx={{
                        color: "rgb(236, 232, 227)",
                        fontFamily: "Montserrat",
                      }}
                    >
                      0.0
                    </Typography>
                  ),
                ]
              )}
            </Box>
            <Box sx={{ pr: { xs: 2, md: 3, xl: 4 } }}>
              <Typography
                sx={{
                  fontSize: "0.75rem",
                  color: "rgb(169, 168, 166)",
                  fontFamily: "Montserrat Light",
                }}
              >
                Volume (24h)
              </Typography>
              <Typography
                sx={{
                  color: "rgb(236, 232, 227)",
                  fontFamily: "Montserrat",
                }}
              >
                5,798,410.3
              </Typography>
            </Box>
            <Box sx={{ pr: { xs: 2, md: 3, xl: 4 } }}>
              <Typography
                sx={{
                  fontSize: "0.75rem",
                  color: "rgb(169, 168, 166)",
                  fontFamily: "Montserrat Light",
                }}
              >
                Funding Rate (8h)
              </Typography>
              <Typography
                sx={{
                  color: "rgb(236, 232, 227)",
                  fontFamily: "Montserrat",
                }}
              >
                0.0071%
              </Typography>
            </Box>
          </Box>
        </Box>
      )}
      {!isDesktop && (
        <>
          <MobilePriceHeader />
          <Box p="16px">
            <Typography
              fontSize="14px"
              textAlign="left"
              fontFamily="Montserrat Medium"
              color="rgb(255, 184, 0)"
            >
              MARKET STATS
            </Typography>
            <Box
              sx={{
                flexGrow: 1,
                mt: 1,
                p: 2,
                border: "1px solid rgba(255, 255, 255, 0.12)",
                borderRadius: "10px",
              }}
            >
              <Grid container spacing={2}>
                <Grid
                  item
                  xs={6}
                  sx={{
                    display: "flex",
                    justifyContent: "flex-start",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "0.75rem",
                        color: "rgb(169, 168, 166)",
                        fontFamily: "Montserrat Light",
                      }}
                    >
                      Index Price
                    </Typography>
                    {isMarketInfoCalculated ? (
                      <Typography
                        sx={{
                          color: "rgb(236, 232, 227)",
                          fontFamily: "Montserrat",
                        }}
                      >
                        {marketIndexPrice}
                      </Typography>
                    ) : (
                      [
                        isMarketInfoInitialized ? (
                          <Typography
                            sx={{
                              color: "rgb(236, 232, 227)",
                              fontFamily: "Montserrat",
                            }}
                          >
                            {marketIndexPrice}
                          </Typography>
                        ) : (
                          <Typography
                            sx={{
                              color: "rgb(236, 232, 227)",
                              fontFamily: "Montserrat",
                            }}
                          >
                            0.0
                          </Typography>
                        ),
                      ]
                    )}
                  </Box>
                </Grid>
                <Grid
                  item
                  xs={6}
                  sx={{
                    display: "flex",
                    justifyContent: "flex-start",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "0.75rem",
                        color: "rgb(169, 168, 166)",
                        fontFamily: "Montserrat Light",
                      }}
                    >
                      Volume (24h)
                    </Typography>
                    <Typography
                      sx={{
                        color: "rgb(236, 232, 227)",
                        fontFamily: "Montserrat",
                      }}
                    >
                      5,798,410.3
                    </Typography>
                  </Box>
                </Grid>
                <Grid
                  item
                  xs={12}
                  sx={{
                    display: "flex",
                    justifyContent: "flex-start",
                  }}
                >
                  <Box>
                    <Typography
                      sx={{
                        fontSize: "0.75rem",
                        color: "rgb(169, 168, 166)",
                        fontFamily: "Montserrat Light",
                      }}
                    >
                      Funding Rate (8h)
                    </Typography>
                    <Typography
                      sx={{
                        color: "rgb(236, 232, 227)",
                        fontFamily: "Montserrat",
                      }}
                    >
                      0.0071%
                    </Typography>
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </Box>
        </>
      )}
    </Box>
  );
}

export default PriceHeader;
