import { useEffect } from "react";

import { useSelector } from "react-redux";
import { IReduxState } from "../../../store/slices/state.interface";
import { calcUserCollateralDetails } from "../../../store/slices/user-slice";
import { useDispatch } from "react-redux";
import { Trade } from "../../../container/trade";
import { AppDispatch } from "../../../store/store";

import useResponsive from "../../../hooks/useResponsive";
import { useWeb3Context } from "../../../hooks/web3Context";

import { Box, Grid, Typography } from "@mui/material";

function CollateralBoard() {
  const dispatch = useDispatch<AppDispatch>();
  const { address, chainID, connected } = useWeb3Context();
  const { perpetualObject } = Trade.useContainer();
  const isDesktop = useResponsive("up", "md");

  const isUserCollateralCalculated = useSelector<IReduxState, boolean>(
    (state) => {
      return state.user.isUserCollateralCalculated;
    }
  );

  const isUserCollateralInitialized = useSelector<IReduxState, boolean>(
    (state) => {
      return state.user.isUserCollateralInitialized;
    }
  );

  const freeCollateral = useSelector<IReduxState, string>((state) => {
    return state.user.freeCollateral;
  });

  const accountValue = useSelector<IReduxState, string>((state) => {
    return state.user.accountValue;
  });

  const CalcUserCollateralDetails = function () {
    if (connected === true) {
      dispatch(
        calcUserCollateralDetails({
          address: address,
          networkID: chainID,
          perpetualObject: perpetualObject,
        })
      );
    }
  };

  useEffect(() => {
    CalcUserCollateralDetails();
    const timer = setInterval(() => CalcUserCollateralDetails(), 60000);
    return () => clearInterval(timer);
  }, [connected]);

  return (
    <>
      <Grid
        container
        spacing={4}
        ml="-16px"
        sx={{
          "& > .MuiGrid-item": { p: "16px 0" },
        }}
      >
        <Grid item lg={6} md={6} sm={6} xs={6}>
          <Box
            py="8px"
            borderRadius="10px"
            ml="16px"
            mr="8px"
            sx={{
              backgroundColor: "rgb(37, 37, 37)",
              border: "1px solid rgba(255, 255, 255, 0.12)",
            }}
          >
            <Typography
              color="rgb(169, 168, 166)"
              fontSize={isDesktop ? "14px" : "12px"}
              lineHeight="1.2"
              fontFamily="Montserrat Light"
            >
              Total Collateral
            </Typography>
            {isUserCollateralCalculated ? (
              <Typography
                color="rgb(236, 232, 227)"
                fontSize="18px"
                fontFamily="Montserrat Medium"
                lineHeight="1.2"
              >
                ${accountValue}
              </Typography>
            ) : (
              [
                isUserCollateralInitialized ? (
                  <Typography
                    color="rgb(236, 232, 227)"
                    fontSize="18px"
                    fontFamily="Montserrat Medium"
                    lineHeight="1.2"
                  >
                    ${accountValue}
                  </Typography>
                ) : (
                  <Typography
                    color="rgb(236, 232, 227)"
                    fontSize="18px"
                    fontFamily="Montserrat Medium"
                    lineHeight="1.2"
                  >
                    $0.0
                  </Typography>
                ),
              ]
            )}
          </Box>
        </Grid>
        <Grid item lg={6} md={6} sm={6} xs={6}>
          <Box
            ml="8px"
            mr="16px"
            sx={{
              paddingY: "8px",
              borderRadius: "10px",
              backgroundColor: "rgb(37, 37, 37)",
              border: "1px solid rgba(255, 255, 255, 0.12)",
            }}
          >
            <Typography
              color="rgb(169, 168, 166)"
              fontSize={isDesktop ? "14px" : "12px"}
              lineHeight="1.2"
              fontFamily="Montserrat Light"
            >
              Free Collateral
            </Typography>
            {isUserCollateralCalculated ? (
              <Typography
                color="rgb(228, 179, 120)"
                fontSize="18px"
                fontFamily="Montserrat Medium"
                lineHeight="1.2"
              >
                ${freeCollateral}
              </Typography>
            ) : (
              [
                isUserCollateralInitialized ? (
                  <Typography
                    color="rgb(228, 179, 120)"
                    fontSize="18px"
                    fontFamily="Montserrat Medium"
                    lineHeight="1.2"
                  >
                    ${freeCollateral}
                  </Typography>
                ) : (
                  <Typography
                    color="rgb(228, 179, 120)"
                    fontSize="18px"
                    fontFamily="Montserrat Medium"
                    lineHeight="1.2"
                  >
                    $0.0
                  </Typography>
                ),
              ]
            )}
          </Box>
        </Grid>
      </Grid>
    </>
  );
}

export default CollateralBoard;
