import { useState, useMemo, useCallback, useEffect } from "react";
import { useSelector } from "react-redux";
import {
  calcPositionDetails,
  openedPositionDetails,
} from "../../../store/slices/position-slice";
import { IReduxState } from "../../../store/slices/state.interface";
import { useDispatch } from "react-redux";
import {
  warning,
  success,
  info,
  error,
} from "../../../store/slices/messages-slice";
import { messages } from "../../../constants/messages";
import { sleep } from "../../../helpers/sleep";
import {
  PerpetualProtocol,
  PositionDraft,
  SupportedChainIds,
} from "@perp/sdk-curie";
import { AppDispatch } from "../../../store/store";

import {
  Box,
  Button,
  CircularProgress,
  TextField,
  Typography,
  InputAdornment,
} from "@mui/material";

import { useWeb3Context } from "../../../hooks/web3Context";
import { Trade } from "../../../container/trade";
import { AMM } from "../../../container/amm";
import { PositionSide } from "@perp/sdk-curie";
import { metamaskErrorWrap } from "../../../helpers/metamask-error-wrap";

function SendTransaction() {
  const dispatch = useDispatch<AppDispatch>();
  const { address, provider, chainID, connected } = useWeb3Context();
  const { marketticker } = AMM.useContainer();
  const { collateral } = Trade.useContainer();
  const { _setCollateral } = Trade.useContainer();
  const { side } = Trade.useContainer();
  const { slippage } = Trade.useContainer();
  const { perpetualObject } = Trade.useContainer();
  const [onTransaction, setOnTransaction] = useState(false);
  const outputSymbol = marketticker.substring(0, marketticker.length - 3);
  const _perpetualObject: PerpetualProtocol = new PerpetualProtocol({
    chainId: SupportedChainIds.OPTIMISM,
    providerConfigs: [
      {
        rpcUrl:
          "https://optimism-mainnet.infura.io/v3/dd583584ac144f7984ac22cb0595646e",
      },
    ],
  });

  const isCalculated = useSelector<IReduxState, boolean>(
    (state) => state.positions.isCalculated
  );
  const newPositionDraft = useSelector<
    IReduxState,
    PositionDraft<string> | undefined
  >((state) => {
    return state.positions.newPositionDraft;
  });
  const oppositeAmount = useSelector<IReduxState, string>((state) => {
    return state.positions.oppositeAmount;
  });
  const entryPrice = useSelector<IReduxState, string>((state) => {
    return state.positions.entryPrice;
  });
  const tradingFee = useSelector<IReduxState, string>((state) => {
    return state.positions.tradingFee;
  });
  const priceImpact = useSelector<IReduxState, string>((state) => {
    return state.positions.priceImpact;
  });

  const isDisabled =
    !isCalculated || collateral === null || collateral.eq(0) || !connected || onTransaction;

  const handleOpen = useCallback(async () => {
    setOnTransaction(true);
    const hasconnected = perpetualObject.hasConnected();
    if (hasconnected === false) {
      await perpetualObject.init();
      await perpetualObject.connect({ signer: provider.getSigner() });
      console.log("Pepr connected");
    }
    let openTx;
    try {
      openTx = await perpetualObject?.clearingHouse?.openPosition(
        newPositionDraft!,
        slippage
      );
      console.log("Open Transaction", openTx);      
      await openTx!.transaction.wait();
      dispatch(success({ text: messages.tx_successfully_send }));
      await sleep(0.1);
      dispatch(info({ text: messages.your_position_update_soon }));
      await dispatch(
        openedPositionDetails({
          address: address,
          networkID: chainID,
          provider: provider,
          perpetualObject: _perpetualObject,
          tickerSymbol: marketticker,
        })
      );
      dispatch(info({ text: messages.your_position_updated }));
      _setCollateral("");
    } catch (err: any) {
      metamaskErrorWrap(err, dispatch);
    } finally {
      setOnTransaction(false);
    }
  }, [newPositionDraft, slippage]);

  const CalcPosition = useCallback(
    (loadProvider) => {
      console.log("Calling calc Position");
      dispatch(
        calcPositionDetails({
          connected: connected,
          networkID: chainID,
          provider: loadProvider,
          perpetualObject: perpetualObject,
          side: side,
          slippage: slippage,
          tickerSymbol: marketticker,
          amountInput: collateral,
        })
      );
    },
    [collateral, slippage, side, marketticker]
  );

  const CalcOpenedPosition = useCallback(
    (loadProvider) => {
      console.log("Calling calc openedPosition");
      dispatch(
        openedPositionDetails({
          address: address,
          networkID: chainID,
          provider: loadProvider,
          perpetualObject: perpetualObject,
          tickerSymbol: marketticker,
        })
      );
    },
    [connected, marketticker]
  );

  useEffect(() => {
    CalcPosition(provider);
  }, [collateral, slippage, side, marketticker]);

  useEffect(() => {
    CalcOpenedPosition(provider);
  }, [connected, marketticker]);

  return (
    <Box sx={{ mt: 1 }}>
      <Typography
        fontSize="13px"
        color="rgb(169, 168, 166)"
        p="4px 0"
        textAlign="left"
        fontFamily="Montserrat Light"
      >
        Output
      </Typography>
      <Box sx={{ mb: 2, backgroundColor: "rgb(11, 11, 11)" }}>
        <TextField
          id="outlined-read-only-input"
          type="number"
          value={oppositeAmount}
          placeholder="0"
          InputProps={{
            readOnly: true,
            endAdornment: (
              <InputAdornment
                position="end"
                sx={{
                  "& .MuiTypography-root": {
                    fontSize: "12px",
                    fontFamily: "Montserrat Light",
                    color: "rgb(169, 168, 166)",
                  },
                }}
              >
                {outputSymbol}
              </InputAdornment>
            ),
          }}
          sx={{
            width: "100%",
            fontFamily: "Montserrat",
            "& > label, & .MuiFormLabel-root.Mui-focused": {
              color: "rgb(169, 168, 166)",
            },
            "& .MuiOutlinedInput-notchedOutline, & .MuiInputBase-root:hover .MuiOutlinedInput-notchedOutline, & .MuiInputBase-root.Mui-focused .MuiOutlinedInput-notchedOutline":
              {
                borderColor: "rgba(255, 255, 255, 0.12)",
                borderWidth: "1",
              },

            "& .MuiInputBase-input": {
              color: "rgb(236, 232, 227)",
              p: "12px 14px",
              fontSize: "16px",
              fontFamily: "Montserrat",
            },
          }}
        />
      </Box>
      <Box
        p="16px"
        border="1px solid rgba(255, 255, 255, 0.12)"
        borderRadius="10px"
      >
        <Typography
          textAlign="left"
          color="rgb(228, 179, 120)"
          fontFamily="Montserrat Medium"
          fontSize="16px"
          pb="8px"
        >
          Summary
        </Typography>
        <Box display="flex" justifyContent="space-between" pb="10px">
          <Typography
            fontSize="13px"
            fontFamily="Montserrat"
            color="rgb(169, 168, 166)"
          >
            Entry Price
          </Typography>
          <Typography
            fontSize="13px"
            fontFamily="Montserrat"
            color="rgb(236, 232, 227)"
          >
            ${entryPrice}
          </Typography>
        </Box>
        <Box display="flex" justifyContent="space-between" pb="10px">
          <Typography
            fontSize="13px"
            fontFamily="Montserrat"
            color="rgb(169, 168, 166)"
          >
            Trading Fee
          </Typography>
          <Typography
            fontSize="13px"
            fontFamily="Montserrat"
            color="rgb(236, 232, 227)"
          >
            ${tradingFee}
          </Typography>
        </Box>
        <Box display="flex" justifyContent="space-between">
          <Typography
            fontSize="13px"
            fontFamily="Montserrat"
            color="rgb(169, 168, 166)"
          >
            Price Impact
          </Typography>
          <Typography
            fontSize="13px"
            fontFamily="Montserrat"
            color="rgb(236, 232, 227)"
          >
            {priceImpact}%
          </Typography>
        </Box>
      </Box>
      <Button
        variant="contained"
        disabled={isDisabled}
        sx={{
          width: "100%",
          paddingY: "12px",
          mt: 2,
          border:
            side === PositionSide.LONG
              ? "1px solid rgb(0, 192, 117)"
              : "1px solid rgb(253, 64, 64)",
          color:
            side === PositionSide.LONG
              ? "rgb(0, 192, 117)"
              : "rgb(253, 64, 64)",
          backgroundColor:
            side === PositionSide.LONG
              ? "rgba(0, 192, 117, 0.05)"
              : "rgba(253, 64, 64, 0.05)",
          display: "block",
          fontFamily: "Montserrat",
          mx: "auto",
          "&: hover": {
            backgroundColor:
              side === PositionSide.LONG
                ? "rgba(0, 192, 117, 0.05)"
                : "rgba(253, 64, 64, 0.05)",
          },
          "&: disabled": {
            backgroundColor: "transparent",
            border: "1px solid rgba(255, 255, 255, 0.12)",
            color: "rgb(169, 168, 166)",
          },
        }}
        onClick={handleOpen}
      >
        {onTransaction ? (
          <Box display="flex" alignItems="center" justifyContent="center">
            <CircularProgress size={24} sx={{ color: "#ffffff" }} />
          </Box>
        ) : (
          `Confirm ${side}`
        )}
      </Button>
    </Box>
  );
}

export default SendTransaction;
