import { useState, useMemo, useCallback, useEffect } from "react";

import {
  Box,
  Button,
  InputAdornment,
  TextField,
  Typography,
} from "@mui/material";

import Big from "big.js";

import { Trade } from "../../../container/trade";
import { Slippage_PRECISION } from "../../../constants/number";
import { formatInput } from "../../../util/format";

import { useDebounce } from "../../../hooks/useDebounce";

function Slippage() {
  const { slippage, setSlippage } = Trade.useContainer();
  const [_slippage, _setSlippage] = useState<string>("");

  const debouncedSlippage = useDebounce({ value: _slippage, delay: 800 });

  const handleOnInput = useCallback(
    (e) => {
      const value = e.target.value;
      if (value >= 0) {
        const formattedValue = formatInput(value, Slippage_PRECISION);
        _setSlippage(formattedValue);
      }
    },
    [_setSlippage]
  );

  useEffect(() => {
    /* reset collateral to null */
    if (debouncedSlippage === "") {
      // setSlippage(null);
      return;
    }
    /* detect if the value is different */
    const b_debouncedSlippage = new Big(debouncedSlippage);
    if (!slippage?.eq(b_debouncedSlippage)) {
      setSlippage(b_debouncedSlippage);
    }
  }, [slippage, debouncedSlippage, setSlippage]);

  return useMemo(
    () => (
      <>
        <Typography
          fontSize="13px"
          color="rgb(169, 168, 166)"
          p="4px 0"
          textAlign="left"
          fontFamily="Montserrat Light"
        >
          Slippage Tolerance
        </Typography>
        <Box display="flex">
          <Box sx={{ mb: 1, backgroundColor: "rgb(11, 11, 11)" }}>
            <TextField
              id="outlined-name"
              type="number"
              value={_slippage}
              placeholder="0.02"
              InputProps={{
                endAdornment: (
                  <InputAdornment
                    position="end"
                    sx={{
                      "& .MuiTypography-root": {
                        fontSize: "12px",
                        fontFamily: "Montserrat Light",
                        color: "rgb(169, 168, 166)",
                      },
                    }}
                  >
                    %
                  </InputAdornment>
                ),
              }}
              onChange={handleOnInput}
              sx={{
                width: "100%",
                fontFamily: "Montserrat",
                "& > label, & .MuiFormLabel-root.Mui-focused": {
                  color: "rgb(169, 168, 166)",
                },
                "& .MuiOutlinedInput-notchedOutline, & .MuiInputBase-root:hover .MuiOutlinedInput-notchedOutline, & .MuiInputBase-root.Mui-focused .MuiOutlinedInput-notchedOutline":
                  {
                    borderColor: "rgba(255, 255, 255, 0.12)",
                    borderWidth: "1",
                  },

                "& .MuiInputBase-input": {
                  color: "rgb(236, 232, 227)",
                  p: "12px 14px",
                  fontSize: "16px",
                  fontFamily: "Montserrat",
                },
              }}
            />
          </Box>
          <Button
            onClick={() => _setSlippage("0.01")}
            sx={{
              width: "47px",
              minWidth: "47px",
              ml: "16px",
              height: "47px",
              p: "8px 10px",
              border: "1px solid rgba(255, 255, 255, 0.12)",
              background: "rbg(37, 37, 37)",
              fontSize: "13px",
              fontFamily: "Montserrat",
              color: "rgb(236, 232, 227)",
            }}
          >
            1%
          </Button>
          <Button
            onClick={() => _setSlippage("0.015")}
            sx={{
              width: "47px",
              minWidth: "47px",
              ml: "16px",
              height: "47px",
              p: "8px 10px",
              border: "1px solid rgba(255, 255, 255, 0.12)",
              background: "rbg(37, 37, 37)",
              fontSize: "13px",
              fontFamily: "Montserrat",
              color: "rgb(236, 232, 227)",
            }}
          >
            1.5%
          </Button>
          <Button
            onClick={() => _setSlippage("0.02")}
            sx={{
              width: "47px",
              minWidth: "47px",
              ml: "16px",
              height: "47px",
              p: "8px 10px",
              border: "1px solid rgba(255, 255, 255, 0.12)",
              background: "rbg(37, 37, 37)",
              fontSize: "13px",
              fontFamily: "Montserrat",
              color: "rgb(236, 232, 227)",
            }}
          >
            2%
          </Button>
        </Box>
      </>
    ),
    [_slippage, handleOnInput]
  );
}

export default Slippage;
