import * as React from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import { IReduxState } from "../../store/slices/state.interface";

import { Box, Button, Typography, Tabs, Tab } from "@mui/material";

import { PositionList } from "./components/PositionList";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ py: 2 }}>{children}</Box>}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

export default function Positions() {
  const [value, setValue] = useState(0);
  const isPositionOpened = useSelector<IReduxState, boolean>(
    (state) => state.positions.isPositionOpened
  );
  console.log("Opened position", isPositionOpened);
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
          textColor="primary"
          TabIndicatorProps={{
            style: {
              backgroundColor: "rgb(236, 232, 227)",
            },
          }}
          sx={{
            minHeight: "36px",
            "& .MuiButtonBase-root": {
              minHeight: "36px",
              p: "6px 16px",
            },
            "& .MuiTab-root": {
              color: "rgb(169, 168, 166)",
              fontFamily: "Montserrat",
              fontSize: "16px",
              textTransform: "none",
            },
            "& .MuiTab-root.Mui-selected": { color: "rgb(236, 232, 227)" },
          }}
        >
          <Tab label="Position" {...a11yProps(0)} />
          <Tab label="Limit Orders" {...a11yProps(1)} />
          <Tab label="Fills" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        {isPositionOpened ? (
          <PositionList />
        ) : (
          <Typography sx={{ color: "rgb(169, 168, 166)" }}>
            You don't have any opened position yet
          </Typography>
        )}
      </TabPanel>
      <TabPanel value={value} index={1}>
        Limit Orders
      </TabPanel>
      <TabPanel value={value} index={2}>
        Fills
      </TabPanel>
    </Box>
  );
}
