import { useCallback } from "react";

import { ButtonGroup, Button } from "@mui/material";

import { PositionSide } from "@perp/sdk-curie";
import { Trade } from "../../../container/trade";

function SideSwitcher() {
  const { side, setPositionSide } = Trade.useContainer();
  const handleLongOnClick = useCallback(() => {
    if (side !== PositionSide.LONG) {
      setPositionSide(PositionSide.LONG);
    }
  }, [setPositionSide, side]);

  const handleShortOnClick = useCallback(() => {
    if (side !== PositionSide.SHORT) {
      setPositionSide(PositionSide.SHORT);
    }
  }, [setPositionSide, side]);

  return (
    <ButtonGroup
      size="large"
      sx={{
        py: "16px",
        border: "none",
        width: "100%",
        "& .MuiButtonGroup-grouped:not(:last-of-type)": {
          borderTopRightRadius: "4px",
          borderBottomRightRadius: "4px",
          borderRightColor:
            side === PositionSide.LONG
              ? "rgb(0, 192, 117)"
              : "rgba(255, 255, 255, 0.12)",
        },
        "& .MuiButtonGroup-grouped:not(:first-of-type)": {
          borderTopLeftRadius: "4px",
          borderBottomLeftRadius: "4px",
          borderLeftColor:
            side === PositionSide.SHORT
              ? "rgb(253, 64, 64)"
              : "rgba(255, 255, 255, 0.12)",
        },
      }}
    >
      <Button
        sx={{
          width: "50%",
          fontSize: "12px",
          fontFamily: "Montserrat",
          p: "10px 20px",
          backgroundColor:
            side === PositionSide.LONG
              ? "rgba(0, 192, 117, 0.05)"
              : "transparent",
          color:
            side === PositionSide.LONG
              ? "rgb(0, 192, 117)"
              : "rgb(169, 168, 166)",
          border:
            side === PositionSide.LONG
              ? "1px solid rgb(0, 192, 117)"
              : "1px solid rgba(255, 255, 255, 0.12)",
          "&: hover": {
            backgroundColor: "rgba(0, 192, 117, 0.05)",
            border: "1px solid rgb(0, 192, 117)",
          },
        }}
        onClick={handleLongOnClick}
      >
        Long
      </Button>
      <Button
        sx={{
          width: "50%",
          fontSize: "12px",
          fontFamily: "Montserrat",
          p: "10px 20px",
          backgroundColor:
            side === PositionSide.SHORT
              ? "rgba(253, 64, 64, 0.05)"
              : "transparent",
          color:
            side === PositionSide.SHORT
              ? "rgb(253, 64, 64)"
              : "rgb(169, 168, 166)",
          border:
            side === PositionSide.SHORT
              ? "1px solid rgb(253, 64, 64)"
              : "1px solid rgba(255, 255, 255, 0.12)",
          "&: hover": {
            backgroundColor: "rgba(253, 64, 64, 0.05)",
            border: "1px solid rgb(253, 64, 64)",
          },
        }}
        onClick={handleShortOnClick}
      >
        Short
      </Button>
    </ButtonGroup>
  );
}

export default SideSwitcher;
