import { Box } from "@mui/material";

import CollateralBalance from "./components/CollateralBalance";
import CollateralBoard from "./components/CollateralBoard";
import useResponsive from "../../hooks/useResponsive";

function Home() {
  const isDesktop = useResponsive("up", "md");
  return (
    <Box
			display="flex"
			justifyContent="center"
			pt="24px"
			px={isDesktop? "24px" : "16px"}
			height={isDesktop? "calc(100vh - 189px)" : "calc(100vh - 168px)"}			
      sx={{
        backgroundColor: "#131312",        
      }}
    >
      <Box sx={{ maxWidth: "1124px", width: "100%" }}>
        <CollateralBalance />
        <CollateralBoard />
      </Box>
    </Box>
  );
}

export default Home;
