import { useEffect, useState } from "react";
import {
  AppBar,
  Box,
  Button,
  Drawer,  
  IconButton,  
  Link,
  Typography,
} from "@mui/material";

import { NavLink } from "react-router-dom";

import { makeStyles } from "@mui/styles";
import classnames from "classnames";
import WalletIcon from "@mui/icons-material/Wallet";
import MenuIcon from "@mui/icons-material/Menu";
import EastIcon from "@mui/icons-material/East";

import { useNavigate } from "react-router-dom";

import { useWeb3Context } from "../hooks/web3Context";
import useResponsive from "../hooks/useResponsive";
import { DEFAULD_NETWORK } from "../constants/blockchain";

type Anchor = "top" | "left" | "bottom" | "right";

const useStyles = makeStyles({
  desktopWrapper: {
    borderBottom: "1px solid rgba(255, 255, 255, 0.16)",
  },

  mobileWrapper: {
    bottom: 0,
    borderTop: "1px solid rgba(255, 255, 255, 0.16)",
  },
});

const menuLists = [
  {
    link: "/account",
    text: "Account",
  },
  {
    link: "/trade",
    text: "Trade",
  },
  {
    link: "/pools",
    text: "Pools",
  },
];

function ConnectMenu() {
  const {
    connect,
    disconnect,
    connected,
    web3,
    address,
    provider,
    providerChainID,
    checkWrongNetwork,
  } = useWeb3Context();
  const [isConnected, setConnected] = useState(connected);

  const [state, setState] = useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const isDesktop = useResponsive("up", "md");
  const classes = useStyles();
  let navigate = useNavigate();

  let buttonText = "Connect";
  let clickFunc: any = connect;

  if (isConnected) {
    buttonText = "Disconnect";
    clickFunc = disconnect;
  }

  if (isConnected && providerChainID !== DEFAULD_NETWORK) {
    buttonText = "Wrong network";
    // buttonStyle = { backgroundColor: "rgb(255, 67, 67)" };
    clickFunc = () => {
      checkWrongNetwork();
    };
  }

  const handleClickMenu = (link: string) => {
    navigate(link);
  };

  const toggleDrawer =
    (anchor: Anchor, open: boolean) =>
    (event: React.KeyboardEvent | React.MouseEvent) => {
      if (
        event.type === "keydown" &&
        ((event as React.KeyboardEvent).key === "Tab" ||
          (event as React.KeyboardEvent).key === "Shift")
      ) {
        return;
      }

      setState({ ...state, [anchor]: open });
    };

  useEffect(() => {
    setConnected(connected);
  }, [web3, connected]);

  const list = (anchor: Anchor) => (
    <Box
      display="flex"
      flexDirection="column"
      justifyContent="space-between"
      flexGrow="1"
      sx={{ width: anchor === "top" || anchor === "bottom" ? "auto" : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <Typography variant="h5" fontWeight="700">
        <Link href="/" sx={{ textDecoration: "none", fontFamily: "Montserrat Medium", color: "rgb(236, 232, 227)" }}>
          DERIVERSE
        </Link>
      </Typography>
      <Box>
        {menuLists.map((menuList, index) => (
          <Box key={menuList.text}>
            <Link
              component={NavLink}
              to={menuList.link}
              sx={{
                display: "flex",
                justifyContent: "space-between",
                textDecoration: "none",
                cursor: "pointer",
              }}
            >
              <Typography color="rgb(169, 168, 166)" fontSize="18px" fontFamily="Montserrat Medium" mb="20px">
                {menuList.text}
              </Typography>
              <EastIcon
                sx={{ color: "rgb(120, 120, 120)", fontSize: "20px" }}
              />
            </Link>
          </Box>
        ))}
      </Box>
    </Box>
  );

  return (
    <AppBar
      position={isDesktop ? "static" : "fixed"}
      className={isDesktop ? classes.desktopWrapper : classes.mobileWrapper}
      sx={{
        backgroundColor: "#131312",
        backgroundImage: "none",
        top: "auto",
        py: 2,
        px: 2,
      }}
    >
      <Box
        display="flex"
        flexGrow={1}
        justifyContent="space-between"
        alignItems="center"
        pl="8px"
      >
        {isDesktop && (
          <Typography variant="h5" fontWeight="700" textAlign="center">
            <Link href="/" sx={{ textDecoration: "none", fontFamily: "Montserrat Bold", color: "rgb(236, 232, 227)" }}>
              DERIVERSE
            </Link>
          </Typography>
        )}
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            flexGrow: "1",
          }}
        >
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={toggleDrawer("bottom", true)}
            sx={{ mr: 2, display: isDesktop ? "none" : "", p: "0 6px" }}
          >
            <MenuIcon sx={{ fontSize: "32px", color: "rgb(120, 120, 120)" }} />
          </IconButton>
          <Drawer
            anchor="bottom"
            open={state["bottom"]}
            onClose={toggleDrawer("bottom", false)}
            sx={{
              "& .MuiDrawer-paper": {
                height: `calc(100vh - 121px)`,
                bottom: "73px",
                backgroundColor: "rgba(9, 9, 10, 1)",
                p: "24px 32px",
              },
            }}
          >
            {list("bottom")}
          </Drawer>
          <Box
            sx={{
              display: isDesktop ? "flex" : "none",
              justifyContent: "space-between",
              "& .MuiTypography-root:hover": { color: "rgb(236, 232, 227)" },
            }}
          >
            {menuLists.map((list, key) => (
              <Typography
                key={key}
                sx={{
                  pl: 4,
                  cursor: "pointer",
                  fontFamily: "Montserrat",
                  color: "rgb(169, 168, 166)",
                }}
                onClick={() => handleClickMenu(list.link)}
              >
                {list.text}
              </Typography>
            ))}
          </Box>
          <Button
            variant="contained"
            sx={{
              cursor: "pointer",
              backgroundColor: "transparent",
              color: "rgba(101, 230, 255, 1)",
              fontSize: "16px",
              "&:hover": { backgroundColor: "rgba(101, 239, 255, 0.12)" },
            }}
            onClick={clickFunc}
          >
            <WalletIcon sx={{ color: "rgba(101, 230, 255, 1)", pr: 1 }} />
            {buttonText}
          </Button>
        </Box>
      </Box>
    </AppBar>
  );
}

export default ConnectMenu;
