import { useState } from "react";
import { useSelector } from "react-redux";
import { IReduxState } from "../../../store/slices/state.interface";
import { useDispatch } from "react-redux";
import {
  warning,
  success,
  info,
  error,
} from "../../../store/slices/messages-slice";
import { close } from "../../../store/slices/position-slice";
import { messages } from "../../../constants/messages";
import { sleep } from "../../../helpers/sleep";
import { PositionSide } from "@perp/sdk-curie";

import { Box, Button, CircularProgress, Grid, Typography } from "@mui/material";

import { useWeb3Context } from "../../../hooks/web3Context";
import { Trade } from "../../../container/trade";
import { AMM } from "../../../container/amm";
import { metamaskErrorWrap } from "../../../helpers/metamask-error-wrap";

export function PositionList() {
  const dispatch = useDispatch();
  const { connected, provider } = useWeb3Context();
  const { perpetualObject } = Trade.useContainer();
  const { slippage } = Trade.useContainer();
  const { marketticker } = AMM.useContainer();
  const [onTransaction, setOnTransaction] = useState(false);
  let tickerSymbol = marketticker;
  const outputSymbol = marketticker.substring(0, marketticker.length - 3);

  const isOpenedPositionCalculated = useSelector<IReduxState, boolean>(
    (state) => state.positions.isOpenedPositionCalculated
  );

  const isLoss = useSelector<IReduxState, boolean>((state) => {
    return state.positions.isLoss;
  });

  const posSize = useSelector<IReduxState, string>((state) => {
    return state.positions.posSize;
  });

  const side = useSelector<IReduxState, PositionSide>((state) => {
    return state.positions.side;
  });

  const pendingValue = useSelector<IReduxState, string>((state) => {
    return state.positions.pendingValue;
  });

  const isProfit = useSelector<IReduxState, boolean>((state) => {
    return state.positions.isProfit;
  });

  const pnlValue = useSelector<IReduxState, string>((state) => {
    return state.positions.pnlValue;
  });

  const openPrice = useSelector<IReduxState, string>((state) => {
    return state.positions.openPrice;
  });

  const indexPrice = useSelector<IReduxState, string>((state) => {
    return state.positions.indexPrice;
  });

  const liquidationPrice = useSelector<IReduxState, string>((state) => {
    return state.positions.liquidationPrice;
  });

  const marginRatio = useSelector<IReduxState, string>((state) => {
    return state.positions.marginRatio;
  });

  const leverage = useSelector<IReduxState, string>((state) => {
    return state.positions.leverage;
  });

  const handleClose = async () => {
    setOnTransaction(true);
    const hasconnected = perpetualObject.hasConnected();
    if (hasconnected === false) {
      await perpetualObject.init();
      await perpetualObject.connect({ signer: provider.getSigner() });
    }
    const position =
      await perpetualObject?.positions?.getTakerPositionByTickerSymbol(
        tickerSymbol
      );
    if (position !== undefined) {
      let closeTx;
      try {
        closeTx = await perpetualObject?.clearingHouse?.closePosition(
          position!,
          slippage
        );
        console.log("Close Transaction", closeTx);
        await closeTx!.transaction.wait();
        dispatch(success({ text: messages.tx_successfully_send }));
        await sleep(0.5);
        dispatch(info({ text: messages.your_position_close_soon }));
      } catch (err: any) {
        metamaskErrorWrap(err, dispatch);
      } finally {
        dispatch(close());
        setOnTransaction(false);
      }
    }
  };

  return (
    <>
      {isOpenedPositionCalculated && (
        <Grid
          container
          spacing={2}
          sx={{
            "& > .MuiGrid-item": { p: "8px" },
            ml: "-8px",
            mt: "8px",
          }}
        >
          <Grid item lg={6} md={6} sm={6} xs={12}>
            <Box
              py="16px"
              px="16px"
              borderRadius="10px"
              border="1px solid rgba(255, 255, 255, 0.12)"
            >
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "flex-start",
                    alignItems: "center",
                  }}
                >
                  <Typography
                    sx={{
                      fontSize: "18px",
                      mr: 1,
                      color: "rgb(236, 232, 227)",
                      fontFamily: "Montserrat Medium",
                    }}
                  >
                    {posSize}
                  </Typography>
                  <Typography
                    sx={{
                      fontSize: "12px",
                      color: "rgb(169, 168, 166)",
                      fontFamily: "Montserrat",
                    }}
                  >
                    {outputSymbol}
                  </Typography>
                </Box>
                <Typography
                  sx={{
                    fontSize: "14px",
                    color:
                      side === "LONG"
                        ? "rgb(127, 212, 130)"
                        : "rgb(239, 104, 104)",
                    fontFamily: "Montserrat",
                  }}
                >
                  {side}
                </Typography>
              </Box>
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Typography
                  sx={{
                    fontSize: "14px",
                    color: "rgb(169, 168, 166)",
                    fontFamily: "Montserrat",
                  }}
                >
                  Pending Funding Payment
                </Typography>
                <Typography
                  sx={{
                    fontSize: "18px",
                    color:
                      isLoss === true
                        ? "rgb(239, 104, 104)"
                        : "rgb(127, 212, 130)",
                    fontFamily: "Montserrat Medium",
                  }}
                >
                  {pendingValue}
                </Typography>
              </Box>
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Typography
                  sx={{
                    fontSize: "14px",
                    color: "rgb(169, 168, 166)",
                    fontFamily: "Montserrat",
                  }}
                >
                  Profit / Loss
                </Typography>
                <Typography
                  sx={{
                    fontSize: "18px",
                    color:
                      isProfit === true
                        ? "rgb(127, 212, 130)"
                        : "rgb(239, 104, 104)",
                    fontFamily: "Montserrat Medium",
                  }}
                >
                  {pnlValue}
                </Typography>
              </Box>
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Typography
                  sx={{
                    fontSize: "14px",
                    color: "rgb(169, 168, 166)",
                    fontFamily: "Montserrat",
                  }}
                >
                  Avg. Open Price
                </Typography>
                <Typography
                  sx={{
                    fontSize: "18px",
                    color: "rgb(255, 184, 0)",
                    fontFamily: "Montserrat Medium",
                  }}
                >
                  {openPrice}
                </Typography>
              </Box>
              <Button
                variant="contained"
                disabled={onTransaction}
                sx={{
                  paddingY: "8px",
                  mt: 1,
                  px: 4,
                  width: "100%",
                  fontFamily: "Montserrat",
                  color: "rgb(236, 232, 227)",
                  backgroundColor: "rgb(37, 37, 37)",
                  border: "1px solid rgba(255, 255, 255, 0.12)",
                  "&: hover": {
                    backgroundColor: "rgb(43, 42, 42)",
                    color: "rgb(236, 232, 227)",
                  },
                }}
                onClick={handleClose}
              >
                {onTransaction ? (
                  <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                  >
                    <CircularProgress size={24} sx={{ color: "#ffffff" }} />
                  </Box>
                ) : (
                  "Close"
                )}
              </Button>
            </Box>
          </Grid>
          <Grid item lg={6} md={6} sm={6} xs={12}>
            <Box
              py="16px"
              px="16px"
              borderRadius="10px"
              border="1px solid rgba(255, 255, 255, 0.12)"
            >
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Typography
                  sx={{
                    fontSize: "14px",
                    color: "rgb(169, 168, 166)",
                    fontFamily: "Montserrat",
                  }}
                >
                  Index Price
                </Typography>
                <Typography
                  sx={{
                    fontSize: "18px",
                    color: "rgb(255, 184, 0)",
                    fontFamily: "Montserrat Medium",
                  }}
                >
                  {indexPrice}
                </Typography>
              </Box>
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Typography
                  sx={{
                    fontSize: "14px",
                    color: "rgb(169, 168, 166)",
                    fontFamily: "Montserrat",
                  }}
                >
                  Liquidation Price
                </Typography>
                <Typography
                  sx={{
                    fontSize: "18px",
                    color: "rgb(255, 184, 0)",
                    fontFamily: "Montserrat Medium",
                  }}
                >
                  {liquidationPrice}
                </Typography>
              </Box>
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Typography
                  sx={{
                    fontSize: "14px",
                    color: "rgb(169, 168, 166)",
                    fontFamily: "Montserrat",
                  }}
                >
                  Margin Ratio
                </Typography>
                <Typography
                  sx={{
                    fontSize: "18px",
                    color: "rgb(255, 184, 0)",
                    fontFamily: "Montserrat Medium",
                  }}
                >
                  {marginRatio}%
                </Typography>
              </Box>
              <Box
                sx={{ display: "flex", justifyContent: "space-between", py: 1 }}
              >
                <Typography
                  sx={{
                    fontSize: "14px",
                    color: "rgb(169, 168, 166)",
                    fontFamily: "Montserrat",
                  }}
                >
                  Leverage
                </Typography>
                <Typography
                  sx={{
                    fontSize: "18px",
                    color: "rgb(255, 184, 0)",
                    fontFamily: "Montserrat Medium",
                  }}
                >
                  {leverage}x
                </Typography>
              </Box>
              <Button
                variant="contained"
                disabled={connected === false}
                sx={{
                  paddingY: "8px",
                  mt: 1,
                  px: 4,
                  width: "100%",
                  fontFamily: "Montserrat",
                  color: "rgb(236, 232, 227)",
                  backgroundColor: "rgb(37, 37, 37)",
                  border: "1px solid rgba(255, 255, 255, 0.12)",
                  "&: hover": {
                    backgroundColor: "rgb(43, 42, 42)",
                    color: "rgb(236, 232, 227)",
                  },
                }}
              >
                Learn More
              </Button>
            </Box>
          </Grid>
        </Grid>
      )}
    </>
  );
}
