import React from "react";
import {
  Box,
  Button,
  Popover,
  MenuItem,
  Stack,
  Typography,
} from "@mui/material";
import { AMM } from "../../../container/amm";

function MobilePriceHeader() {
  const MarketList = ["BTCUSD", "ETHUSD", "BNBUSD", "AVAXUSD"];

  const { marketticker, setMarketTicker } = AMM.useContainer();

  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(
    null
  );

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleChange = (data: string) => {
    setMarketTicker(data);
    setAnchorEl(null);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  return (
    <>
      <Button
        onClick={handleClick}
        sx={{
          display: "flex",
          height: "55px",
          justifyContent: "space-between",
          alignItems: "center",
          width: "100%",
          p: "4px 16px",
          boxSizing: "border-box",
          borderBottom: "1px solid rgba(255, 255, 255, 0.12)",
          background: "rgb(37, 37, 37)",
          borderRadius: "0",
        }}
      >
        <Box>
          <Typography
            color="rgb(120, 120, 120)"
            lineHeight="1.2"
            fontSize="12px"
          >
            Market
          </Typography>
          <Typography color="rgb(236, 232, 227)" lineHeight="1.2">
            {marketticker}
          </Typography>
        </Box>
        <Box>
          <Typography color="rgb(120, 120, 120)">$1638.04</Typography>
        </Box>
      </Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        sx={{
          "& .MuiPopover-paper": {
            left: "0 !important",
            top: "60px !important",
            width: "100%",
            maxWidth: '100%',
            borderRadius: "0",            
          },
        }}
      >
        <Stack
          sx={{
            background: "rgb(37, 37, 37)",
            border: "1px solid rgba(255, 255, 255, 0.12)",
          }}
        >
          {MarketList.map((data) => (
            <MenuItem
              key={data}
              onClick={() => handleChange(data)}
              sx={{
                display: "flex",
                justifyContent: "space-between",
                color: "rgb(236, 232, 227)",
                borderBottom: "1px solid rgba(255, 255, 255, 0.12)",
                py: 1,
                fontFamily: "Montserrat Medium",
                "&:last-child": { borderBottom: "none" },
                boxSizing: "border-box",
              }}
            >
              <Typography>{data}</Typography>
              <Typography color="rgb(120, 120, 120)">$1638.04</Typography>
            </MenuItem>
          ))}
        </Stack>
      </Popover>
    </>
  );
}

export default MobilePriceHeader;
