import { useMemo, useCallback, useEffect } from "react";

import { Box, TextField, InputAdornment, Typography } from "@mui/material";

import Big from "big.js";

import { Trade } from "../../../container/trade";
import { USDC_PRECISION } from "../../../constants/number";
import { formatInput } from "../../../util/format";

import { useDebounce } from "../../../hooks/useDebounce";

function Collateral() {
  const { collateral, setCollateral } = Trade.useContainer();
  const { _collateral, _setCollateral } = Trade.useContainer();

  const debouncedCollateral = useDebounce({ value: _collateral, delay: 800 });

  const handleOnInput = useCallback(
    (e) => {
      const value = e.target.value;
      if (value >= 0) {
        const formattedValue = formatInput(value, USDC_PRECISION);
        _setCollateral(formattedValue);
      }
    },
    [_setCollateral]
  );

  useEffect(() => {
    /* reset collateral to null */
    if (debouncedCollateral === "") {
      // setCollateral(null);
      return;
    }
    /* detect if the value is different */
    const b_debouncedCollateral = new Big(debouncedCollateral);
    if (!collateral?.eq(b_debouncedCollateral)) {
      setCollateral(b_debouncedCollateral);
    }
  }, [collateral, debouncedCollateral, setCollateral]);

  return useMemo(
    () => (
      <>
        <Typography
          fontSize="13px"
          color="rgb(169, 168, 166)"
          p="4px 0"
          textAlign="left"
          fontFamily="Montserrat Light"
        >
          Collateral
        </Typography>
        <Box sx={{ mb: 2, backgroundColor: "rgb(11, 11, 11)" }}>
          <TextField
            id="outlined-name"
            type="number"
            value={_collateral}
            placeholder="0"
            InputProps={{
              endAdornment: (
                <InputAdornment
                  position="end"
                  sx={{
                    "& .MuiTypography-root": {
                      fontSize: "12px",
                      fontFamily: "Montserrat Light",
                      color: "rgb(169, 168, 166)",
                    },
                  }}
                >
                  USDC
                </InputAdornment>
              ),
            }}
            onChange={handleOnInput}
            sx={{
              width: "100%",
              fontFamily: "Montserrat",
              "& > label, & .MuiFormLabel-root.Mui-focused": {
                color: "rgb(169, 168, 166)",
              },
              "& .MuiOutlinedInput-notchedOutline, & .MuiInputBase-root:hover .MuiOutlinedInput-notchedOutline, & .MuiInputBase-root.Mui-focused .MuiOutlinedInput-notchedOutline":
                {
                  borderColor: "rgba(255, 255, 255, 0.12)",
                  borderWidth: "1",
                },

              "& .MuiInputBase-input": {
                color: "rgb(236, 232, 227)",
                p: "12px 14px",
                fontSize: "16px",
                fontFamily: "Montserrat",
              },
            }}
          />
        </Box>
      </>
    ),
    [_collateral, handleOnInput]
  );
}

export default Collateral;
